@media (max-width: 500px) {
    .App {
        display: flex;
        
        flex-direction: column;
        align-items:center;

        background-color:var(--background--1);
        min-height: 100vh;
        min-width: 100vw;
        font-family: 'Courier New', Courier, monospace;
        gap: 2em;
        color: var(--text-second);
        font-family: Arial, sans-serif;
        text-align: center;
      }

 
    .Card {
        color: var(--text-main);
        border-radius: 5px;
        border: 1px solid var(--text-second);
        padding: 0.3em;
        max-width: 30vw;
        max-height: 20vh;
        height: 10vh;
        width: 30vw;
        font-size: 0.70em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: var(--background--2);
        transition: all 0.3s ease;
        
      }

      .App-header 
        {
        background-color: var(--background--1);
        width: 100vw;
        height: 13vh;
        display: flex;
        padding-left: 5vw;
        }


      .cards-section
        {
        display: flex;
        width: 100vw;
        padding: 0 5vw 0 5vw;
        gap: 0.5em;
        color: var(--text-main);
        }
        .game-status
        {
            flex: 1;
            display: flex;
            justify-content: space-around;
            align-items: center;
            font-size: 0.70em;   
            padding: 1em 0 1em 0;
        }
        .game-status h3
        {
            border-bottom: 1px solid var(--text-second);
            padding: 1em;
        }
        .reset-btn
        {
            font-size:0.70em;
        }

        .chessboard-container {
            display: flex;
            flex-direction: row;
            gap: 0.3em;
            justify-content: center;
            flex: 2;
            gap:0.5em;
          }

          .board_recap-container
            {
            display: flex;
            flex-direction: column;
            gap: 1em;
           
            justify-content: space-evenly;
            
            }

            .move-recap-component
            {
                max-height: 40vh;
                height: 30vh;
                max-width: 100vw;
            }
      

  }